import { onMounted, ref, computed, watch } from "vue";
import { useRouter } from "vue-router";
import { MessageBox, Setting, User, Service, View as IcView, Help, Check, OfficeBuilding } from "@element-plus/icons-vue";
export default {
  components: {
    MessageBox,
    Setting,
    User,
    Service,
    IcView,
    Help,
    Check,
    OfficeBuilding
  },
  props: {
    modelValue: {
      type: Boolean,
      required: true
    }
  },

  setup() {
    const router = useRouter();
    const activeMenu = ref("");
    const currentRoute = computed(() => router.currentRoute.value.path + router.currentRoute.value.hash);
    watch(currentRoute, () => {
      activeMenu.value = currentRoute.value;
    });

    const handleSelect = key => {
      router.push(key);
    };

    const size = ref("20%");

    const handleSize = () => {
      if (window.innerWidth <= 600) {
        size.value = "100%";
      } else if (window.innerWidth <= 850) {
        size.value = "40%";
      } else if (window.innerWidth <= 1100) {
        size.value = "30%";
      } else {
        size.value = "20%";
      }
    };

    onMounted(() => {
      window.addEventListener("resize", handleSize);
      handleSize();
      activeMenu.value = router.currentRoute.value.fullPath;
    });
    return {
      handleSelect,
      size,
      activeMenu
    };
  }

};