import "core-js/modules/es.parse-int.js";
import "core-js/modules/es.json.stringify.js";
import "core-js/modules/web.dom-collections.iterator.js";
import "core-js/modules/web.url.js";
import "core-js/modules/web.url-search-params.js";
import { ref, watch, onMounted, onBeforeUnmount } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import { io } from "socket.io-client";
import { UserFilled, Menu, RefreshRight } from "@element-plus/icons-vue";
import { notification, errorPopup } from "../../../shared/notifications.js";
import BaseMenu from "../components/BaseMenu.vue";
import DashboardFilterForm from "../components/DashboardFilterForm.vue";
import DashboardTable from "../components/DashboardTable.vue";
import axios from "../../../shared/axios.js";
export default {
  components: {
    BaseMenu,
    DashboardFilterForm,
    DashboardTable
  },

  setup() {
    const store = useStore();
    const router = useRouter();
    const filter = ref(false);
    const filterCount = ref(1);
    const totalRows = ref(1);
    const currentPage = ref(1);
    const pageSize = ref(50);
    const drawer = ref(false);

    const toggleDrawer = () => {
      drawer.value = !drawer.value;
    };

    const getPages = () => {
      axios.post("/dashboard/table/count", store.getters.filters).then(response => {
        totalRows.value = parseInt(response.data.count, 10);
      }).catch(() => {
        errorPopup("Упс! Не удалось загрузить страницы!");
      });
    };

    const reportDrawer = ref(false);

    const toggleReportDrawer = () => {
      reportDrawer.value = !reportDrawer.value;
    };

    const reportForm = ref({
      created_at: true,
      defects: true,
      cabinet: true,
      done_at: true,
      technician: true,
      status: true,
      performed_works: true,
      client: true,
      client_phone: true
    });

    const getReport = () => {
      if (Object.keys(reportForm.value).some(key => reportForm.value[key])) {
        axios.get("/dashboard/report", {
          params: {
            filters: JSON.stringify(store.getters.optionsAndFilters),
            columns: JSON.stringify(reportForm.value)
          },
          responseType: "blob"
        }).then(response => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", "Report.xlsx");
          document.body.appendChild(link);
          link.click();
        }).catch(() => {
          errorPopup("Упс! Не удалось загрузить отчет!");
        });
      } else {
        errorPopup("Пожалуйста, укажите хотя бы одно поле");
      }
    };

    const update = ref(false);

    const toggleUpdate = () => {
      getPages();
      update.value = !update.value;
    };

    watch([currentPage, pageSize], () => {
      store.dispatch("setOptions", {
        limit: pageSize.value,
        page: currentPage.value
      });
    });
    watch(filter, () => {
      if (!filter.value) {
        for (let i = 1; i <= filterCount.value; i++) {
          store.dispatch("removeFilters", i);
        }

        getPages();
      }

      filterCount.value = 1;
    });

    const logout = () => {
      axios.post("/dashboard/auth/logout").then(() => {
        router.push("/dashboard/login");
      });
    };

    const handleFilter = filterParam => {
      if (filterParam.delete) {
        store.dispatch("removeFilters", {
          id: filterParam.id
        });
        filterCount.value = filterParam.id;
      } else {
        store.dispatch("addFilters", filterParam);
        filterCount.value = filterCount.value > filterParam.id + 1 || filterParam.id > 7 ? filterCount.value : filterParam.id + 1;
      }

      getPages();
    };

    const handleError = () => {
      errorPopup("Упс! Что-то пошло не так");
    };

    const socket = io(process.env.NODE_ENV === "production" ? "https://mtec-support.herokuapp.com/" : "http://localhost:3000", {
      transports: ["websocket"]
    });
    socket.on("row:new", () => {
      store.dispatch("setUpdated");
      getPages();
      notification("Новая заявка", "Появилась новая заявка!");
    });
    onBeforeUnmount(() => {
      socket.disconnect();
    });
    onMounted(() => {
      getPages();
    });
    return {
      filter,
      filterCount,
      drawer,
      toggleDrawer,
      reportDrawer,
      toggleReportDrawer,
      reportForm,
      getReport,
      update,
      toggleUpdate,
      totalRows,
      currentPage,
      pageSize,
      logout,
      handleFilter,
      handleError,
      UserFilled,
      Menu,
      RefreshRight
    };
  }

};